import React, { useState, useEffect, useRef, useContext } from "react";
import InnerTitle from "./InnerTitle";
import styles from "../style/Main.module.css";
import searchIcon from "../img/search.png";
import addressIcon from "../img/addressIcon.png";
import slash from "../img/slash.png";
import { perPersonKRW } from "../utils/price";
import { useNavigate, useLocation } from "react-router-dom";
import { getDirectImageUrl, FALLBACK_IMG } from "../utils/image";
import { API_BASE } from "../config";
import { UserContext } from "../context/UserContext";
import { useUnread } from "../context/UnreadContext";

function Main() {
  const navigate = useNavigate();
  const location = useLocation();

  const initialCategory = location.state?.category || "";
  const { userAddress } = useContext(UserContext);

  // / 
  const [keyword, setKeyword] = useState("");     // 
  const [category, setCategory] = useState(initialCategory);   // "", "", "" ...

  // :    
  const [fullItems, setFullItems] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");

  //    ()
  const PAGE_SIZE = 20;           //     
  const [viewCount, setViewCount] = useState(PAGE_SIZE);

  //  
  const debounceRef = useRef(null);

  const [notifications, setNotifications] = useState([]);
  const [readStates, setReadStates] = useState({});
  const { setUnreadNotis } = useUnread();

  useEffect(() => {
    const fetchNotifications = async () => {
      try {
        const token = localStorage.getItem("jwt"); //    
        const res = await fetch(`${API_BASE}/notifications`, {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
            "Authorization": `Bearer ${token}`,
          },
        });

        if (res.ok) {
          const data = await res.json();
          setNotifications(data.items || []);
          setReadStates(
            (data.items || []).reduce((acc, noti) => {
              acc[noti.id] = noti.isRead;
              return acc;
            }, {})
          );
          const totalUnread = data.items.filter(noti => !noti.isRead).length;
          setUnreadNotis(totalUnread);
        } else {
          console.error("  :", res.status);
        }
      } catch (error) {
        console.error("  :", error);
      }
    };

    fetchNotifications();
  }, []);

  const [chatrooms, setChatrooms] = useState([]);
  const [err, setErr] = useState(null);
  const { setUnreadMessages } = useUnread();

  const getAuthHeaders = () => {
    const token = localStorage.getItem("jwt");
    return token ? { Authorization: `Bearer ${token}` } : {};
  };

  useEffect(() => {
    const controller = new AbortController();

    (async () => {
      try {
        setLoading(true);
        setErr(null);

        const res = await fetch(`${API_BASE}/me/chatrooms`, {
          method: "GET",
          headers: {
            ...getAuthHeaders(),
          },
          credentials: "include",
          signal: controller.signal,
        });

        if (!res.ok) throw new Error(`HTTP ${res.status}`);
        const data = await res.json();

        setChatrooms(Array.isArray(data) ? data : []);
        const totalUnread = data.reduce((sum, room) => sum + (room.unreadCount || 0), 0);
        setUnreadMessages(totalUnread);
      } catch (e) {
        if (e.name !== "AbortError") setErr(e);
      } finally {
        setLoading(false);
      }
    })();

    return () => controller.abort();
  }, []);

  //   
  function normalizePage(data) {
    if (Array.isArray(data?.items)) return data.items;     // /posts/search  items  
    if (Array.isArray(data?.content)) return data.content; // /posts  content  
    if (Array.isArray(data)) return data;                  //  
    return [];
  }

  const { totalUnread } = useUnread();
  const [notice, setNotice] = useState(null);

  useEffect(() => {
    if (totalUnread > 0) {
      setNotice("  / .");
      // 5   
      const timer = setTimeout(() => setNotice(null), 5000);
      return () => clearTimeout(timer);
    }
  }, [totalUnread]);

  // Main.js

  //  (  )  
  async function fetchAll({ reset = false } = {}) {
    try {
      setLoading(true);
      setError("");

      const params = new URLSearchParams();
      params.set("status", "OPEN");
      if (category) params.append("category", category);

      const baseUrl = keyword.trim()
        ? `${API_BASE}/posts/search`
        : category
          ? `${API_BASE}/posts/search/category`
          : `${API_BASE}/posts`;
      if (keyword.trim()) params.set("q", keyword.trim());

      const token = localStorage.getItem("jwt"); //     
      const headers = { "Content-Type": "application/json" };
      if (token) headers.Authorization = `Bearer ${token}`;

      // 1.    
      const res = await fetch(`${baseUrl}?${params.toString()}`, {
        method: "GET",
        headers,
      });
      if (!res.ok) throw new Error(`HTTP ${res.status}`);

      const data = await res.json();
      let all = normalizePage(data);

      //    
      if (userAddress && userAddress.trim()) {
        all = all.filter(
          (post) =>
            (post.roadAddress || post.author?.roadAddress || "").includes(userAddress)
        );
      }

      // 2.       (Post.js  )
      async function getCount(id) {
        const r = await fetch(`${API_BASE}/posts/${id}/applications/count`, {
          method: "GET",
          headers,
        });
        if (!r.ok) return 0; //   0 
        const body = await r.json().catch(() => null);
        if (typeof body === "number") return body;
        if (body && typeof body.count === "number") return body.count;
        return Number(body?.count ?? 0) || 0;
      }

      //    
      const concurrency = 8;
      const out = [];
      for (let i = 0; i < all.length; i += concurrency) {
        const chunk = all.slice(i, i + concurrency);
        const counted = await Promise.all(
          chunk.map(async (it) => {
            const applicantCount = await getCount(it.id);
            // Post.js  '(1) +  '   
            const fixedCount = applicantCount;
            return {
              ...it,
              currentMemberCount: fixedCount,
            };
          })
        );
        out.push(...counted);
      }
      const filtered = out.filter(
        (item) => item.currentMemberCount < item.desiredMemberCount
      );


      // 3.   state 
      setFullItems(filtered);

      if (reset) setViewCount(PAGE_SIZE);

    } catch (e) {
      setError(e.message || " ");
      setFullItems([]);
    } finally {
      setLoading(false);
    }
  }

  // /    
  useEffect(() => {
    if (debounceRef.current) clearTimeout(debounceRef.current);
    setLoading(true);
    setError("");

    debounceRef.current = setTimeout(() => {
      fetchAll({ reset: true });
    }, 300);

    return () => {
      if (debounceRef.current) clearTimeout(debounceRef.current);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [keyword, category]);

  //  
  useEffect(() => {
    fetchAll({ reset: true });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  //  (/Enter):   
  const onClickSearch = async () => {
    if (debounceRef.current) clearTimeout(debounceRef.current);
    await fetchAll({ reset: true });
  };
  const onKeyDown = (e) => {
    if (e.key === "Enter") {
      e.preventDefault();
      onClickSearch();
    }
  };

  //   
  const visibleItems = fullItems.slice(0, viewCount);
  const hasMoreToShow = viewCount < fullItems.length;

  return (
    <div className={styles.mainWrapper}>
      {notice && (
        <div className={styles.toast}>
          {notice}
        </div>
      )}
      <InnerTitle />
      <div className={styles.main}>
        <section className={styles.searchWrap}>
          <div className={styles.searchInner}>
            <select
              className={styles.categorySelect}
              value={category}
              onChange={(e) => setCategory(e.target.value)}
            >
              <option value=""></option>
              <option value="">#</option>
              <option value="">#</option>
              <option value="">#</option>
              <option value="">#</option>
              <option value="">#</option>
            </select>

            <input
              className={styles.searchInput}
              placeholder=" "
              value={keyword}
              onChange={(e) => setKeyword(e.target.value)}
              onKeyDown={onKeyDown}
            />
            <button
              className={styles.searchIcon}
              aria-label=""
              onClick={onClickSearch}
            >
              <img className={styles.searchimg} src={searchIcon} alt="" />
            </button>
          </div>
        </section>

        {loading && <div className={styles.loading}> ...</div>}
        {error && !loading && (
          <div className={styles.error}>  : {error}</div>
        )}

        {!loading && !error && (
          <>
            <section className={styles.list}>
              {visibleItems.map((item) => (
                <article key={item.id} className={styles.card}>
                  <img
                    className={styles.thumb}
                    src={getDirectImageUrl(item.mainImageUrl || item.imageUrl)}
                    alt={item.title || "thumbnail"}
                    loading="lazy"
                    onError={(e) => {
                      e.currentTarget.src = FALLBACK_IMG;
                      e.currentTarget.onerror = null;
                    }}
                  />
                  <div className={styles.right}>
                    <header className={styles.cardHead}>
                      <div
                        className={styles.title}
                        onClick={() => navigate(`/post/${item.id}`)}
                      >
                        {item.title}
                      </div>
                      <span className={styles.pill}>
                        {item.currentMemberCount}/{item.desiredMemberCount}
                      </span>
                    </header>

                    <div className={styles.under}>
                      <div className={styles.cardBody}>
                        <div className={styles.icon}>
                          <p className={styles.category}>#{item.category}</p>
                          <div className={styles.address}>
                            <img
                              className={styles.addressIcon}
                              src={addressIcon}
                              alt=""
                            />
                            <div className={styles.roadaddress}>
                              {item.roadAddress !== undefined && item.roadAddress !== null
                                ? item.roadAddress
                                : item.author?.roadAddress || " "}
                            </div>
                          </div>
                        </div>
                        <div className={styles.content}>{item.content}</div>
                      </div>

                      <aside className={styles.priceBox}>
                        <div className={styles.price}>
                          {perPersonKRW(
                            item.productDesc,
                            item.desiredMemberCount
                          )}
                        </div>
                        <img className={styles.slash} src={slash} alt="/" />
                        <div className={styles.totalPrice}>
                          total {item.productDesc}
                        </div>
                      </aside>
                    </div>
                  </div>
                </article>
              ))}
            </section>

            {/*  :    */}
            {hasMoreToShow && (
              <div style={{ display: "flex", justifyContent: "center", padding: "16px" }}>
                <button
                  className={styles.moreBtn}
                  onClick={() => setViewCount((v) => v + PAGE_SIZE)}
                >
                  
                </button>
              </div>
            )}

            {/*    */}
            <button
              onClick={() => {
                navigate("/write");
              }}
              className={styles.sideBtn}
              aria-label=""
            >
              
            </button>
          </>
        )}
      </div>
    </div>
  );
}

export default Main;
