import React from "react";
import { useNavigate } from "react-router-dom";
import Title from "./Title";
import styles from "../style/Home.module.css";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/effect-coverflow";
import { Navigation, EffectCoverflow, Autoplay } from "swiper/modules";
//   
import introImg from "../img/exampleIMG.png";
import introImg1 from "../img/thumb2.png";
import introImg2 from "../img/thumb.png";
import introImg3 from "../img/thumb3.png";

//   import .
import thumb1 from "../img/thumb1.png";
import thumb2 from "../img/thumb2.png";
import thumb3 from "../img/thumb3.png";

function Home() {
    const navigate = useNavigate();
    //       2 .
    const images = [thumb1, thumb2, thumb3, thumb1, thumb2, thumb3];


    return (
        <div className={styles.Home}>
            <Title />
            {/* Swiper   .   3  . */}
            <div className={styles.sliderContainer}>
                <Swiper
                    modules={[Autoplay]}
                    loop={true}
                    slidesPerView={'auto'}
                    centeredSlides={true}
                    autoplay={{
                        delay: 3000,
                        disableOnInteraction: false,
                    }}
                    className={styles.mySwiper}
                >
                    {images.map((img, index) => (
                        <SwiperSlide key={index} className={styles.swiperSlide}>
                            <img src={img} alt={`slide ${index + 1}`} />
                        </SwiperSlide>
                    ))}
                </Swiper>
            </div>


            <div className={styles.loginBtn}>
                <button className={styles.login} onClick={() => navigate("/login")}>
                    
                </button>
                <span>|</span>
                <button className={styles.register} onClick={() => navigate("/register")}>
                    
                </button>
            </div>
        </div>
    );
}

export default Home;