import React, { useEffect, useMemo, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import InnerTitle from "./InnerTitle";
import styles from "../style/Write.module.css";
import { API_BASE } from "../config";
import Swal from "sweetalert2";

const CATEGORIES = ["", "", "", "", ""];

export default function Write() {
  const navigate = useNavigate();

  // form state
  const [title, setTitle] = useState("");
  const [productName, setProductName] = useState("");
  const [price, setPrice] = useState("");
  const [people, setPeople] = useState(2);
  const [url, setUrl] = useState("");
  const [category, setCategory] = useState("");
  const [desc, setDesc] = useState("");
  const [image, setImage] = useState(null); // { file, preview }
  const fileRef = useRef(null);

  const titleLimit = 20;
  const titleCount = useMemo(() => `${title.length}/${titleLimit}`, [title]);

  const selectCategory = (name) => {
    setCategory((prev) => (prev === name ? "" : name));
  };

  const onSelectImage = (e) => {
    const file = e.target.files?.[0];
    if (!file) return;
    if (!file.type.startsWith("image/")) {
      Swal.fire({
        icon: "warning",
        text: "  .",
        confirmButtonText: "",
        confirmButtonColor: "#1f8954ff"
      });
      return;
    }
    const objectUrl = URL.createObjectURL(file);
    setImage({ file, preview: objectUrl });
    e.target.value = "";
  };

  //  URL 
  useEffect(() => {
    return () => {
      if (image?.preview) URL.revokeObjectURL(image.preview);
    };
  }, [image]);

  const validate = () => {
    if (!title.trim()) return { ok: false, msg: " ." };
    if (title.trim().length > titleLimit)
      return { ok: false, msg: ` ${titleLimit}  .` };
    if (!productName.trim()) return { ok: false, msg: " ." };
    if (!price.trim()) return { ok: false, msg: " ." };
    if (isNaN(Number(price.replaceAll(",", ""))))
      return { ok: false, msg: "  ." };
    if (!category) return { ok: false, msg: " ." };
    return { ok: true };
  };

  // 1)     2)  URL mainImageUrl 
  async function uploadImage(file, token) {
    const form = new FormData();
    form.append("file", file, file.name);

    const res = await fetch(`${API_BASE}/files`, {
      method: "POST",
      headers: {
        Authorization: `Bearer ${token}`, //    
        // Content-Type  : FormData  (boundary )
      },
      body: form,
    });
    if (!res.ok) throw new Error(`   (HTTP ${res.status})`);
    const data = await res.json(); // { url: "https://..." } 
    if (!data?.url) throw new Error("  url ");
    return data.url;
  }

  const onSubmit = async (e) => {
    e.preventDefault();
    const v = validate();
    if (!v.ok) {
      Swal.fire({
        icon: "warning",
        text: v.msg,
        confirmButtonText: "",
        confirmButtonColor: "#1f8954ff"
      });
      return;
    }

    try {
      const token = localStorage.getItem("jwt");
      if (!token) {
        alert("  .  .");
        return;
      }

      // 1)      URL 
      let mainImageUrl = null;
      if (image?.file) {
        mainImageUrl = await uploadImage(image.file, token);
      }

      // 2)  (JSON)
      const body = {
        title: title.trim(),
        category,
        productName: productName.trim(),
        productUrl: url.trim() || null,
        productDesc: price.trim(),
        desiredMemberCount: Number(people),
        content: desc.trim(),
        mainImageUrl,                              //  URL ( null)
      };

      const res = await fetch(`${API_BASE}/posts`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify(body),
      });
      if (!res.ok) throw new Error(`HTTP ${res.status}`);
      const saved = await res.json();
      Swal.fire({
        icon: "success",
        text: "  .",
        confirmButtonText: "",
        confirmButtonColor: "#1f8954ff",
      });
      navigate(-1);
    } catch (err) {
      console.error(err);
      alert(`   : ${err.message || err}`);
    }
  };

  return (
    <div className={styles.writewrapper}>
      <InnerTitle />
      <main className={styles.container}>
        <section className={styles.card}>
          <form className={styles.form} onSubmit={onSubmit}>
            {/*  */}
            <div className={styles.row}>
              <label className={styles.label}></label>
              <div className={styles.flexGrow}>
                <input
                  className={styles.input}
                  type="text"
                  placeholder="20  ..."
                  value={title}
                  maxLength={titleLimit}
                  onChange={(e) => setTitle(e.target.value)}
                />
                <span className={styles.counter}>{titleCount}</span>
              </div>
            </div>

            {/*  /  */}
            <div className={styles.rowFour}>
              <label className={styles.label}></label>
              <input
                className={`${styles.input} ${styles.inputSmall}`}
                type="text"
                value={productName}
                onChange={(e) => setProductName(e.target.value)}
              />
              <label className={styles.label}></label>
              <input
                className={`${styles.input} ${styles.inputPrice}`}
                type="text"
                inputMode="numeric"
                placeholder=" "
                value={price}
                onChange={(e) => {
                  const raw = e.target.value.replace(/[^0-9]/g, "");
                  if (raw === "") {
                    setPrice("");
                    return;
                  }
                  const formatted = Number(raw).toLocaleString("ko-KR");
                  setPrice(formatted);
                }}
              />

            </div>

            {/*   / URL */}
            <div className={styles.rowFour}>
              <label className={styles.label}> </label>
              <select
                className={`${styles.input} ${styles.inputPeople}`}
                value={people}
                onChange={(e) => setPeople(Number(e.target.value))}
              >
                {Array.from({ length: 29 }, (_, i) => i + 2).map((n) => (
                  <option key={n} value={n}>
                    {n}
                  </option>
                ))}
              </select>
              <label className={styles.label}>URL</label>
              <input
                className={`${styles.input} ${styles.url} ${styles.flexGrow}`}
                type="url"
                placeholder=""
                value={url}
                onChange={(e) => setUrl(e.target.value)}
              />
            </div>

            {/*  */}
            <div className={styles.row}>
              <label className={styles.label}></label>
              <div className={styles.categoryWrap}>
                {CATEGORIES.map((name) => {
                  const active = category === name;
                  return (
                    <button
                      key={name}
                      type="button"
                      className={`${styles.chip} ${active ? styles.chipOn : ""}`}
                      onClick={() => selectCategory(name)}
                      aria-pressed={active}
                    >
                      #{name}
                    </button>
                  );
                })}
              </div>
            </div>

            {/*  */}
            <div className={styles.row}>
              <label className={styles.label}></label>
              <textarea
                className={`${styles.textarea} ${styles.flexGrow}`}
                rows={6}
                placeholder=" ..."
                value={desc}
                onChange={(e) => setDesc(e.target.value)}
              />
            </div>

            {/*  */}
            <div className={styles.row}>
              <label className={styles.label}></label>
              <div className={styles.uploadWrap}>
                {image ? (
                  <div className={styles.previewWrap}>
                    <img
                      src={image.preview}
                      alt=""
                      className={styles.preview}
                    />
                    <button
                      type="button"
                      className={styles.deleteBtn}
                      onClick={() => setImage(null)}
                    >
                      
                    </button>
                  </div>
                ) : (
                  <button
                    type="button"
                    className={styles.uploadBox}
                    onClick={() => fileRef.current?.click()}
                  >
                    <span className={styles.plus}></span>
                  </button>
                )}

                <input
                  ref={fileRef}
                  type="file"
                  accept="image/*"
                  onChange={onSelectImage}
                  style={{ display: "none" }}
                />
              </div>
            </div>

            {/*  */}
            <div className={styles.footer}>
              <button
                type="button"
                className={`${styles.btn} ${styles.btnGhost}`}
                onClick={() => navigate(-1)}
              >
                
              </button>
              <button type="submit" className={`${styles.btn} ${styles.btnPrimary}`}>
                
              </button>
            </div>
          </form>
        </section>
      </main>
    </div>
  );
}
